@import "https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@300;400;600;700&family=Roboto+Slab:wght@500;700&display=swap";
:root {
  --primary: #2563eb;
  --primary-dark: #1e3a8a;
  --accent: #f97316;
  --mint: #22c55e;
  --bg: #f8fafc;
  --bg-alt: #eef2ff;
  --text: #0f172a;
  --muted: #475569;
  --card: #fff;
  --shadow: 0 20px 50px #0f172a1f;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  color: var(--text);
  background: var(--bg);
  margin: 0;
  padding: 0;
  font-family: Noto Sans SC, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section, footer {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

.section {
  padding: 6rem 0;
}

.section.alt {
  background: var(--bg-alt);
}

.section-inner {
  width: min(1100px, 90vw);
  margin: 0 auto;
}

.section-header {
  flex-direction: column;
  gap: .75rem;
  margin-bottom: 2.5rem;
  display: flex;
}

.section-header.light h2, .section-header.light p {
  color: #f8fafc;
}

.section-subtitle {
  color: var(--muted);
  font-size: 1.05rem;
}

h1, h2, h3 {
  margin: 0;
  font-family: Roboto Slab, Noto Sans SC, sans-serif;
  font-weight: 700;
}

h1 {
  font-size: clamp(2.6rem, 4vw, 4.5rem);
  line-height: 1.1;
}

h1 span {
  color: var(--muted);
  font-size: clamp(1.4rem, 2.5vw, 2rem);
  font-weight: 500;
  display: block;
}

h2 {
  font-size: clamp(1.6rem, 2.6vw, 2.4rem);
}

h3 {
  font-size: 1.2rem;
}

p {
  color: var(--text);
  margin: 0;
  font-size: 1rem;
  line-height: 1.7;
}

p.small {
  color: var(--muted);
  font-size: .9rem;
}

ul {
  margin: .5rem 0 0;
  padding-left: 1.2rem;
}

li {
  color: var(--text);
  line-height: 1.7;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: var(--primary);
}

.site-header {
  width: 100%;
  z-index: 20;
  backdrop-filter: blur(12px);
  background: #f8fafcd9;
  border-bottom: 1px solid #94a3b833;
  position: fixed;
  top: 0;
}

.nav-links {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.2rem;
  padding: 1rem 2rem;
  font-size: .95rem;
  display: flex;
}

.hero {
  color: #f8fafc;
  background: radial-gradient(circle at 100% 0, #3b82f659, #0000 55%), linear-gradient(120deg, #0f172a 10%, #1e3a8a 55%, #2563eb 100%);
  align-items: center;
  display: flex;
  overflow: hidden;
}

.hero-glow {
  pointer-events: none;
  background: radial-gradient(circle at 20% 20%, #22c55e59, #0000 40%);
  position: absolute;
  inset: 0;
}

.hero-content {
  width: min(1200px, 92vw);
  z-index: 1;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2.5rem;
  margin: 0 auto;
  padding: 7rem 0 5rem;
  display: grid;
  position: relative;
}

.hero-text h2 {
  color: #e2e8f0;
  font-weight: 500;
}

.hero-text p {
  color: #e2e8f0;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: .2em;
  color: #c7d2fe;
  font-size: .75rem;
}

.hero-quote {
  margin-top: 1rem;
  font-size: 1.1rem;
}

.hero-intro {
  color: #cbd5f5;
  margin-top: 1rem;
}

.cta-group {
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 2rem;
  display: flex;
}

.btn {
  border: 1px solid #0000;
  border-radius: 999px;
  justify-content: center;
  align-items: center;
  gap: .4rem;
  padding: .75rem 1.4rem;
  font-weight: 600;
  transition: transform .2s, box-shadow .2s;
  display: inline-flex;
}

.btn.primary {
  background: var(--primary);
  color: #fff;
  box-shadow: 0 12px 25px #2563eb66;
}

.btn.accent {
  background: var(--accent);
  color: #fff;
  box-shadow: 0 12px 25px #f9731666;
}

.btn.ghost {
  color: #e2e8f0;
  background: none;
  border-color: #e2e8f0b3;
}

.btn.full {
  width: 100%;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 16px 35px #0f172a40;
}

.hero-card {
  backdrop-filter: blur(16px);
  box-shadow: var(--shadow);
  background: #0f172a99;
  border: 1px solid #94a3b84d;
  border-radius: 24px;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  display: flex;
}

.avatar {
  width: 140px;
  height: 140px;
  background: linear-gradient(135deg, #22c55e, #3b82f6, #f97316);
  border: 6px solid #f8fafc40;
  border-radius: 50%;
  align-self: center;
}

.hero-meta {
  text-align: center;
}

.meta-title {
  color: #f8fafc;
  font-size: 1.05rem;
}

.meta-sub {
  color: #cbd5f5;
  margin-top: .4rem;
  font-size: .95rem;
}

.meta-tags {
  flex-wrap: wrap;
  justify-content: center;
  gap: .5rem;
  margin-top: 1rem;
  display: flex;
}

.meta-tags span {
  background: #f8fafc26;
  border-radius: 999px;
  padding: .3rem .8rem;
  font-size: .8rem;
}

.scroll-indicator {
  opacity: .8;
  animation: float 2s ease-in-out infinite;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.scroll-indicator img {
  width: 36px;
  height: 36px;
}

@keyframes float {
  0%, 100% {
    transform: translate(-50%);
  }

  50% {
    transform: translate(-50%, 10px);
  }
}

.card-grid {
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1.5rem;
  display: grid;
}

.card {
  background: var(--card);
  box-shadow: var(--shadow);
  border: 1px solid #94a3b833;
  border-radius: 20px;
  padding: 1.8rem;
  transition: transform .2s, box-shadow .2s;
}

.card.center {
  text-align: center;
}

.card:hover {
  transform: translateY(-6px);
  box-shadow: 0 25px 60px #0f172a2e;
}

.quote-card {
  background: linear-gradient(120deg, #2563eb1a, #0e74901a);
  border: 1px solid #2563eb33;
  border-radius: 20px;
  margin-top: 2rem;
  padding: 2rem;
}

.split {
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
  display: grid;
}

.contact {
  color: #f8fafc;
  padding: 5rem 0 3rem;
}

.contact-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  display: grid;
}

.contact-card {
  background: #0f172abf;
  border: 1px solid #94a3b840;
  border-radius: 20px;
  padding: 2rem;
}

.contact-card h3 {
  color: #f8fafc;
  margin-bottom: 1rem;
}

.contact-list ul {
  margin-top: .5rem;
}

.contact-list li {
  color: #e2e8f0;
}

.social-row {
  gap: 1rem;
  margin-top: 1.5rem;
  display: flex;
}

img.socialIcon {
  height: 28px;
  width: 28px;
  filter: brightness(0) invert();
}

.contact-form {
  flex-direction: column;
  gap: .75rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.contact-form label {
  color: #e2e8f0;
  font-size: .85rem;
}

.contact-form input, .contact-form textarea {
  color: #f8fafc;
  background: #0f172a66;
  border: 1px solid #94a3b866;
  border-radius: 12px;
  padding: .7rem .9rem;
}

.contact-form input::placeholder, .contact-form textarea::placeholder {
  color: #e2e8f0b3;
}

.qr-placeholder {
  text-align: center;
  margin-top: 1rem;
}

.qr-box {
  color: #e2e8f0;
  border: 1px dashed #e2e8f099;
  border-radius: 12px;
  padding: 1.2rem;
}

.footer-note {
  text-align: center;
  color: #e2e8f0b3;
  margin-top: 2.5rem;
}

@media (max-width: 900px) {
  .hero-content {
    padding-top: 6rem;
  }

  .nav-links {
    gap: .8rem;
    font-size: .85rem;
  }
}

@media (max-width: 600px) {
  .cta-group {
    flex-direction: column;
  }

  .hero-card {
    text-align: center;
    align-items: center;
  }
}

